(function() {
  'use strict';

  angular
    .module('cathy', ['ngCookies', 'ngRoute', 'ngMaterial',
      'ngMessages', 'angular-loading-bar', 'ngAnimate'
    ]);

})();

(function() {
  'use_strict';

  angular
    .module('cathy')
    .factory('userService', userService);

  /** @ngInject */
  function userService($http, $log, catherineAPI, authenticationService) {
    var apiHost = catherineAPI.apiHost;

    var service = {
      register: register
    };

    return service;

    function register(name, username, password, callback) {
      return $http.post(apiHost + '/api/users/', {
        name: name,
        username: username,
        password: password
      }).then(registerComplete).catch(registerFailed);

      function registerComplete(response) {
        if (response.status == 200) {
          authenticationService.login(username, password, callback);
        }
        else {
          callback(false);
        }
      }

      function registerFailed(error) {
        $log.error('XHR Failed for register.\n' + angular.toJson(error.data, true));
        callback(false);
      }
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('cathy')
    .directive('cathyNavbar', cathyNavbar);

  /** @ngInject */
  function cathyNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController($location, authenticationService) {
      var vm = this;

      vm.signOut = signOut;

      function signOut() {
        authenticationService.clearToken();
        $location.path('/');
      }
    }
  }

})();

(function() {
  'use_strict';

  angular
    .module('cathy')
    .factory('catherineAPI', catherineAPI);

  /** @ngInject */
  function catherineAPI() {
    var apiHost = 'http://catherine-api.herokuapp.com';

    var service = {
      apiHost: apiHost
    };

    return service;
  }
})();

(function() {
  'use_strict';

  angular
    .module('cathy')
    .factory('categoryService', categoryService);

  /** @ngInject */
  function categoryService($rootScope, $http, $log, catherineAPI) {
    var apiHost = catherineAPI.apiHost;

    var service = {
      add: add,
      update: update,
      remove: remove,
      get: get
    };

    return service;

    function add(name, icon_id, callback) {
      if (!icon_id) {
        icon_id = 'tag';
      }

      return $http.post(apiHost + '/api/' + $rootScope.globals.currentUser.username + '/categories/', {
        name: name,
        icon_id: icon_id
      }).then(addComplete).catch(addFailed);

      function addComplete(response) {
        callback(response.data);
      }

      function addFailed(error) {
        $log.error('XHR Failed for add.\n' + angular.toJson(error.data, true));
        callback(error);
      }
    }

    function get(id, callback) {
      if (id) {
        return $http.get(apiHost + '/api/' + $rootScope.globals.currentUser.username + '/categories/' + id)
          .then(getComplete).catch(getFailed);
      } else {
        return $http.get(apiHost + '/api/' + $rootScope.globals.currentUser.username + '/categories/')
          .then(getComplete).catch(getFailed);
      }

      function getComplete(response) {
        callback(response.data);
      }

      function getFailed(error) {
        $log.error('XHR Failed for get.\n' + angular.toJson(error.data, true));
        callback(error);
      }
    }

    function remove(id, callback) {
      return $http.delete(apiHost + '/api/' + $rootScope.globals.currentUser.username + '/categories/' + id)
        .then(removeComplete).catch(removeFailed);

      function removeComplete(response) {
        if (response.status == 200) {
          callback(true);
        }
        else {
          callback(false);
        }
      }

      function removeFailed(error) {
        $log.error('XHR Failed for remove.\n' + angular.toJson(error.data, true));
        callback(error);
      }
    }

    function update(id, category, callback) {
      return $http.put(apiHost + '/api/' + $rootScope.globals.currentUser.username + '/categories/' + id,
          category)
        .then(updateComplete).catch(updateFailed);

      function updateComplete(response) {
        callback(response.data);
      }

      function updateFailed(error) {
        $log.error('XHR Failed for update.\n' + angular.toJson(error.data, true));
        callback(error);
      }
    }
  }
})();

(function() {
  'use_strict';

  angular
    .module('cathy')
    .factory('authenticationService', authenticationService);

  /** @ngInject */
  function authenticationService($http, $log, $cookies, $rootScope, $window, catherineAPI) {
    var apiHost = catherineAPI.apiHost;

    var service = {
      login: login,
      setToken: setToken,
      clearToken: clearToken,
      parseJwt: parseJwt
    };

    return service;

    function login(username, password, callback) {
      return $http.post(apiHost + '/api/login/', {
        username: username,
        password: password
      }).then(loginComplete).catch(loginFailed);

      function loginComplete(response) {
        var token = response.data.token;
        setToken(token);
        callback(true);
      }

      function loginFailed(error) {
        $log.error('XHR Failed for login.\n' + angular.toJson(error.data, true));
        callback(false);
      }
    }

    function setToken(token) {
      var payload = parseJwt(token);
      $rootScope.globals = {
        currentUser: {
          name: payload.name,
          username: payload.username,
          token: token
        }
      };
      $http.defaults.headers.common.Authorization = 'Bearer ' + token;
      $cookies.putObject('globals', $rootScope.globals);
    }

    function clearToken() {
      $rootScope.globals = {};
      $http.defaults.headers.common.Authorization = '';
      $cookies.remove('globals');
    }

    function parseJwt(token) {
      var base64Url = token.split('.')[1];
      var base64 = base64Url.replace('-', '+').replace('_', '/');
      return angular.fromJson($window.atob(base64));
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('cathy')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($log, $mdToast, $location, $rootScope, userService, authenticationService) {
    var vm = this;

    vm.signUp = signUp;
    vm.signIn = signIn;

    function signUp() {
      var name = vm.register.name;
      var username = vm.register.username;
      var password = vm.register.password;
      userService.register(name, username, password, function(response) {
        if (response === true) {
          $mdToast.show(
            $mdToast.simple()
            .textContent($rootScope.globals.currentUser.name + ' registered and logged.')
            .hideDelay(3000)
          );
          $location.path('/dashboard');
        }
        else {
          $mdToast.show(
            $mdToast.simple()
            .textContent('Something is wrong!')
            .hideDelay(3000)
          );
        }
      });
    }

    function signIn() {
      var username = vm.username;
      var password = vm.password;
      authenticationService.login(username, password, function(response) {
        if (response === true) {
          $mdToast.show(
            $mdToast.simple()
            .textContent($rootScope.globals.currentUser.name + ' logged.')
            .hideDelay(3000)
          );
          $location.path('/dashboard');
        }
        else {
          $mdToast.show(
            $mdToast.simple()
            .textContent('Username and password does not match.')
            .hideDelay(3000)
          );
        }
      });
    }

  }
})();

(function() {
  'use strict';

  angular
    .module('cathy')
    .controller('DashboardController', DashboardController);

  /** @ngInject */
  function DashboardController($location, authenticationService) {
    var vm = this;

    vm.signOut = signOut;

    function signOut() {
      authenticationService.clearUser();
      $location.path('/');
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('cathy')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log, $rootScope, $location, $cookies, $http, authenticationService) {
    $rootScope.globals = $cookies.getObject('globals') || {};

    if ($rootScope.globals.currentUser) {
      var token = $rootScope.globals.currentUser.token;
      var payload = authenticationService.parseJwt(token);
      $log.log((Math.round(new Date().getTime() / 1000) <= payload.exp), angular.toJson(payload))
      if (Math.round(new Date().getTime() / 1000) <= payload.exp) {
        $http.defaults.headers.common.Authorization = 'Bearer ' + token;
      }
    }

    $rootScope.$on('$locationChangeStart', function(event, next, current) {
      // redirect to login page if not logged in and trying to access a restricted page
      var restrictedPage = $location.path() != '/';
      var loggedIn = $rootScope.globals.currentUser;
      if (restrictedPage && !loggedIn) {
        $location.path('/');
      }
      else if ($location.path() == '/' && loggedIn) {
        $location.path('/dashboard');
      }
    });

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('cathy')
    .config(routeConfig);

  function routeConfig($routeProvider) {
    $routeProvider
      .when('/', {
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'vm'
      })
      .when('/dashboard', {
        templateUrl: 'app/dashboard/dashboard.html',
        controller: 'DashboardController',
        controllerAs: 'vm'
      })
      .otherwise({
        redirectTo: '/'
      });
  }

})();

